form {
    width: 90%;
    max-width: 300px;
    background-color: #fff;
    padding: 33px 40px;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
}
label {
    text-align: center;
    font-weight: bold;
    font-size: 2.0rem;
    color: #1e1e1e;
    margin-bottom: 28px;
}
textarea {
    background-color: #f9fbfc;
    border: 2px solid #202329;
    border-radius: 4px;
    box-shadow: 0px 5px 50px rgba(32,35, 41,0.25);
    color: #1e1e1f;
    font-family: 'Caveat', cursive;
    font-size: 2.0rem;
    text-align: center;
    padding: 12px;
    height: 96px;
    width: calc(100% - 25px);
}
textarea::placeholder {
    color: #a5a5a5;
    font-family: 'Caveat', cursive;
    font-weight: 400;
}
textarea:focus {
    outline-color: #61dafa;
}
.TodoForm-buttonContainer {
    margin-top: 14px;
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100%;
}
.TodoForm-button {
    cursor: pointer;
    display: inline-block;
    font-size: 2.0rem;
    color: #202329;
    font-family: 'Caveat', cursive;
    font-weight: 600;
    width: 120px;
    height: 48px;
    border-radius: 4px;
    border: none;
    
}
.TodoForm-button--add {
    background: #61dafa;
    box-shadow: 0px 5px 25px rgba(97, 218, 250, 0.5);
}
.TodoForm-button--cancel {
    background: transparent;
}